import Vue from 'vue'
import Router from 'vue-router'

if (!window.Router) Vue.use(Router)

export default new Router({
    /*base: '/aoxinwechat/',
    mode: 'history',*/
    routes: [{
        path: '/',
        redirect: '/login'
    }, {
        path: '/login',
        component: () => import('@/views/login.vue')
    }, {
        path: '/home',
        component: () => import('@/views/home.vue'),
        children: [{
            /*首页*/
            path: '/',
            component: () => import('@/views/home/index.vue')
        }, {
            /*公司信息管理*/
            path: '/home/manage',
            component: () => import('@/views/company/index.vue')
        }, {
            /*公司信息管理*/
            path: '/home/company_s',
            component: () => import('@/views/company/company_s.vue')
        }, {
            /*公司详情*/
            path: '/home/company_details',
            component: () => import('@/views/company/company_details.vue')
        }, {
            /*新增产品*/
            path: '/home/add_product',
            component: () => import('@/views/products/index.vue')
        }, {
            /*产品提交成功*/
            path: '/home/product_add_c',
            component: () => import('@/views/products/product_add_c.vue')
        }, {
            /*产品发布成功*/
            path: '/home/product_fb_c',
            component: () => import('@/views/products/product_fb_c.vue')
        }, {
            /*产品管理*/
            path: '/home/product_manage',
            component: () => import('@/views/products/product_manage.vue')
        }, {
            /*产品详情*/
            path: '/home/product_details',
            component: () => import('@/views/products/product_details.vue')
        }, {
            /*产品类型*/
            path: '/home/product_type',
            component: () => import('@/views/products/product_type.vue')
        }, {
            /*产品编辑*/
            path: '/home/product_edit',
            component: () => import('@/views/products/product_edit.vue')
        }, {
            /*新建新闻*/
            path: '/home/add_news',
            component: () => import('@/views/news/index.vue')
        }, {
            /*新闻发布成功*/
            path: '/home/news_fb_c',
            component: () => import('@/views/news/news_fb_c.vue')
        }, {
            /*新闻保存成功*/
            path: '/home/news_bc_c',
            component: () => import('@/views/news/news_bc_c.vue')
        }, {
            /*新闻管理*/
            path: '/home/news_manage',
            component: () => import('@/views/news/news_manage.vue')
        }, {
            /*新闻详情*/
            path: '/home/news_d',
            component: () => import('@/views/news/news_d.vue')
        }, {
            /*新闻编辑*/
            path: '/home/news_edit',
            component: () => import('@/views/news/news_edit.vue')
        }, {
            /*新闻版块*/
            path: '/home/news_section',
            component: () => import('@/views/news/news_section.vue')
        }, {
            /*新建岗位*/
            path: '/home/new_post',
            component: () => import('@/views/recruitment/index.vue')
        }, {
            /*岗位发布成功*/
            path: '/home/post_fb',
            component: () => import('@/views/recruitment/post_fb.vue')
        }, {
            /*岗位保存成功*/
            path: '/home/post_bc_c',
            component: () => import('@/views/recruitment/post_bc_c.vue')
        }, {
            /*岗位管理*/
            path: '/home/post_manage',
            component: () => import('@/views/recruitment/post_manage.vue')
        }, {
            /*岗位详情*/
            path: '/home/post_d',
            component: () => import('@/views/recruitment/post_d.vue')
        }, {
            /*编辑岗位*/
            path: '/home/post_edit',
            component: () => import('@/views/recruitment/post_edit.vue')
        }, {
            /*岗位类型*/
            path: '/home/post_type',
            component: () => import('@/views/recruitment/post_type.vue')
        }, {
            /*新建账号*/
            path: '/home/new_account',
            component: () => import('@/views/system/index.vue')
        }, {
            /*账号管理*/
            path: '/home/account_manage',
            component: () => import('@/views/system/account_manage.vue')
        },{
            /*账号修改*/
            path: '/home/account_edit',
            component: () => import('@/views/system/account_edit.vue')
        }, {
            /*账号修改成功*/
            path: '/home/account_xg_c',
            component: () => import('@/views/system/account_xg_c.vue')
        }, {
            /*账户添加成功*/
            path: '/home/account_add_s',
            component: () => import('@/views/system/account_add_s.vue')
        }, {
            /*修改密码*/
            path: '/home/modify_password',
            component: () => import('@/views/system/modify_password.vue')
        }, {
            /*密码修改成功*/
            path: '/home/sub_password',
            component: () => import('@/views/system/sub_password.vue')
        }, {
            /*操作日志*/
            path: '/home/operation_log',
            component: () => import('@/views/system/operation_log.vue')
        }]
    }]
})