/*登录验证*/
router.beforeEach((to, from, next) => {
    if (to.path === '/login') {
        next();
    } else {
        let token = localStorage.getItem('Authorization')
        if (token === null || token === '') {
            next('/login');
        } else {
            next();
        }
    }
})
new Vue({
    render: h => h(App),
    router,
    store,
}).$mount('#app')